import React from 'react'
import styled from 'styled-components'
import { FlexWrapper } from './styledComponents'
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';
import { IWeather } from '../api/weather';
import { getWeatherIconUrl } from './WeatherCardSm';

import { WeatherFeatureCollection } from './WeatherFeature';

const LocationText = styled.p`
	font-size: 16px;
	color: #ffffff;
	margin-left: 2px;
`

const PrimaryCardWrapper = styled.div`
	height: 280px;
	width: 100%;
	background-color: ${props => props.color || props.theme.mainColor};
	border-radius: 12px;
	padding: 12px;
	box-sizing: border-box;
	color: #ffffff;
`

const WeatherIcon = styled.img`
	width: 120px;
	position: relative;
	bottom: 28px;
`

const InfoWrapper = styled.div`
	text-align: center;
	position: relative;
`

const InforDetails = styled.div`
	position: absolute;
	width: 100%;
	top: 72px;
`

const DateString = styled.p`
	font-size: 12px;
`

const TempText = styled.p`
	font-size: 48px;
	font-weight: 600;
	margin: 6px 0;
`

const WeatherText = styled.p`
	font-size: 14px;
`

const BottomWrapper = styled.div`
	margin: 10px 36px;
`

const Location = (props: { location: string }) => {

	return (
		<FlexWrapper>
			<LocationOnOutlinedIcon htmlColor='#ffffff' fontSize='small' />
			<LocationText>{props.location}</LocationText>
		</FlexWrapper>
	)
}

export interface DetailCardProps extends IWeather {
	color?: string
}

const PrimaryCard = (props: DetailCardProps) => {

	const date = new Date()

	return (
		<PrimaryCardWrapper color={props.color}>
			<Location location={props.name} />
			<InfoWrapper>
				<WeatherIcon src={getWeatherIconUrl(props.weather[0].icon)} alt={props.weather[0].description} />
				<InforDetails>
					<DateString>{`Today, ${date.toLocaleString(undefined, { day: 'numeric', month: 'long' })}`}</DateString>
					<TempText>{props.main.temp.toFixed()}<span>&#176;</span></TempText>
					<WeatherText>{props.weather[0].main}</WeatherText>
					<BottomWrapper>
						<WeatherFeatureCollection windSpeed={props.wind.speed} humidity={props.main.humidity} />
					</BottomWrapper>
				</InforDetails>
			</InfoWrapper>
		</PrimaryCardWrapper>
	)
}

export default PrimaryCard